.art:hover {
  cursor: pointer;
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

.art.selected {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.galleryInfo {
  text-align: center;
  height: 150px;
  padding: 0;
  margin-top: -10px;

}
.artsite:hover{
  color: #9d9d9d;
}
.artsite{
  color: #00c9a4;
}



